import { Component, EventEmitter, Input, Output } from '@angular/core';
import * as country from './countrycodes.json';

@Component({
  selector: 'angular-weblineindia-phone-number',
  templateUrl: './angular-weblineindia-phone-number.component.html',
  styles: [],
})
export class AngularWeblineindiaPhoneNumberComponent {
  @Input() name!: string;
  @Input() value!: string;
  @Input() phoneNumberInput!: string;
  @Input() countrySelectBox!: string;
  @Input() inputBox!: string;
  @Input() errorMessage!: string;
  @Input() placeholder!: string;
  @Input() disabled: boolean = false;
  @Input() require!: boolean;
  @Input() type!: string;
  @Input() isInvalidPhoneNumber: boolean = false;
  @Input() invalidMessage!: string;
  @Input() countryCode: string = '+91';

  @Output() focus: EventEmitter<void> = new EventEmitter<void>();
  @Output() blur: EventEmitter<void> = new EventEmitter<void>();
  @Output() keyPress: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();
  @Output() keyDown: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();
  @Output() keyUp: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();
  @Output() countrySelected: EventEmitter<void> = new EventEmitter<void>();

  countryData: any = country;
  selectedCountry: any = {};

  ngOnInit(): void {
    this.setDefaultCountry();
  }

  setDefaultCountry(): void {
    this.selectedCountry = this.countryData.default.find((countries: any) =>
      [this.countryCode].includes(countries.dial_code)
    );

    // If selectedCountry is not found, default it to +91
    if (!this.selectedCountry) {
      this.selectedCountry = this.countryData.default.find((countries: any) =>
        '+91'.includes(countries.dial_code)
      );
    }
  }

  onFocus(event: any): void {
    this.focus.emit(event);
  }

  onBlur(event: any): void {
    this.blur.emit(event);
  }

  onKeyPress(event: KeyboardEvent) {
    this.keyPress.emit(event);
  }

  onKeyDown(event: KeyboardEvent) {
    this.keyDown.emit(event);
  }

  onKeyUp(event: KeyboardEvent) {
    this.keyUp.emit(event);
  }

  onCountrySelected(event: any): void {
    const countryCode = event.target.value;
    this.selectedCountry = this.countryData.default.find(
      (country: any) => country.dial_code === countryCode
    );
    this.countrySelected.emit(this.selectedCountry);
  }
}
